.about {
	margin-top: 0px;
}

@media only screen and (min-width: 576px) and (max-width: 992px) {
	.about {
		margin-top: 100px;
	}
}

.about-content {
	margin-left: var(--MARGIN-DEFAULT);
	margin-right: var(--MARGIN-DEFAULT);
}

.about-header {
	color: var(--MAIN-COLOR);
	text-align: right;
}

.about-item {
	position: relative;
}

.about-features-inner {
	background-color: var(--MAIN-COLOR);
	position: relative;
}